/* Task Screen Style */
.create-task-wrapper {
    padding: 20px 20px;
    position: relative;
}

.task-done-wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.create-task-form-icon {
    background-image: url("./assets/icon/icons8-brief-96.png");
    background-size: 40px 40px;
    width: 40px;
    height: 40px;
    background-repeat: no-repeat;
}

.description-input-wrapper input, textarea {
    direction: rtl;
}

.custom-selection .ant-select-selection__rendered {
    display: flex;
    justify-content: flex-end;
    font-size: 13px;
}

.task-done-wrapper-header h4 {
    font-weight: bold;
}

.task-in-progress-wrapper-header h4 {
    font-weight: bold;
}

.task-undone-wrapper-header h4 {
    font-weight: bold;
}

.task-done-wrapper-body-item {
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.1);
    padding: 20px 10px;
    transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
    border-right: 3px solid;
    border-radius: 5px;
}

.task-done-wrapper-body-item:hover {
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
    cursor: pointer;
}

.task-inProgress-wrapper-body-item {
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.1);
    padding: 20px 10px;
    transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
    border-right: 3px solid #AB44F3;
    border-radius: 5px;
}

.task-inProgress-wrapper-body-item:hover {
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
    cursor: pointer;
}

.task-undone-wrapper-body-item {
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.1);
    padding: 20px 10px;
    transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
    border-right: 3px solid #4257c4;
    border-radius: 5px;
}

.task-undone-wrapper-body-item:hover {
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
    cursor: pointer;
}

.modal-header-title-text {
    text-align: end;
}

.ant-modal-close {
    left: 0px;
}

.ant-modal-footer {
    display: flex;

}

.p-task-description-editor {
    padding: 8px;
    text-align: justify;
    margin-top: 9px;
    border: 1px solid rgba(0, 0, 0, 0.1);
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.07);
}

.last-data-fetched-time {
    position: absolute;
    bottom: 0;
    left: 15px;
    background-color: #AB44F3;
    padding: 5px 15px;
    border-radius: 50px;
    color :white;
}